import Vue from "vue";
import Vuex from "vuex";
import TopicsModule from "./modules/topics-module.js";
import ConfigsModule from "./modules/configs-module.js";

Vue.use(Vuex);


export default new Vuex.Store({
  state: {
    cid: -100,
  },
  getters: {
    cid: (state) => state.cid,
  },
  mutations: {
    setCid: (state, cid) => (state.cid = cid),
  },
  actions: {
    changeCid({ commit }, cid) {
      commit("setCid", cid);
    },
  },
  modules: {
    TopicsModule,
    ConfigsModule
  },
});
