<style scoped>
  @import "@/assets/css/project.css";
</style>

<template>
    <div>
   
         <v-row :class="index==0?'flex-row':'flex-row-reverse top_row'"   v-for="(def,index) in getDefs"
                  :key="index">
                <v-col xxl="7" xl="7" lg="7" md="7" sm="7" cols="12"
                
