       </div>
  
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name:"AboutTopic",
     methods: {
    ...mapActions(["fetchTopics"]),
  },
  computed:{
    ...mapGetters(["getDefs"]),
    
  },
  mounted() {
