<style scoped>
  @import "@/assets/css/sliderBackground.css";
</style>

<template>
    <div>
      
          <v-carousel hide-delimiters height="auto" >
          <template v-slot:prev="{ on, attrs }">
             <v-img :src="require('@/assets/images/home/arrow_left.png')"  v-bind="attrs"
              v-on="on" class="btn_arrow_left" max-width="29" max-height="39"></v-img>
            </template>
            <template v-slot:next="{ on, attrs }">
              <v-img :src="require('@/assets/images/home/arrow_right.png')" class="btn_arrow_right" 
              v-bind="attrs"   v-on="on"></v-img>
            </template>
            <template v-for="topic in getSlider" >
             <v-carousel-item  v-for="(image,index) in topic.images"  :src="`${image.link}`" :key="index"     cover >
                     <div class="d-flex flex-column fill-height align-center justify-center" height="500" cover>
                        <v-card flat color="transparent" class="card_background">
                           <v-img src="../assets/images/home/logo-big.png"  
                             max-width="260" max-height="248"  class="mx-auto">
                           </v-img>
                      
                           <v-card-subtitle class="text_background">
                            <p>
                               {{topic.title}}
                            </p>
                          </v-card-subtitle>
                      </v-card>
                     
                    </div>
                
            </v-carousel-item>
           </template>
          </v-carousel>
    </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name:"BackgroundComponent",
  methods: {
    ...mapActions(["fetchTopics"]),
  },
  computed:{
    ...mapGetters(["getSlider"]),
   },
  mounted() {
   let slider={
    topic_type_id:10,
    cat:"slider"
   }
   this.fetchTopics(slider);
 },
}
</script>