
<template>
  <div>
    <v-navigation-drawer v-model="drawer" fixed app temporary>
      <v-list dense>
        <v-list-item-group v-for="(item, i) in items" :key="i" color="primary">
          <v-list-item v-if="!item.submenu" :to="item.to">
            <v-list-item-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title v-text="item.title.toUpperCase()" />
            </v-list-item-content>
          </v-list-item>
          <v-list-group v-else :prepend-icon="item.icon" no-action>
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title
                  v-text="item.title.toUpperCase()"
                ></v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item
              v-for="child in item.submenu"
              :key="child.title"
              :to="child.to"
            >
              <v-list-item-content>
                <v-list-item-title v-text="child.title"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar fixed app   height="auto" elevate-on-scroll class="header" v-on:scroll.native="handleScroll" >
      <v-app-bar-nav-icon class="hidden-md-and-up" @click="drawer = true" />
      <router-link to="/" class="d-flex">
        <v-img
          class="logo"
          src="@/assets/images/home/logo-small.png"
          alt=""
          max-width="92"
          max-height="92"
          >
          </v-img>
       </router-link>
      <v-spacer />
     
        <v-btn
          link
          v-for="(item,i) in items" 
          :key="i"
          depressed
          tile
          plain
          class="py-8 hidden-sm-and-down btn_title"
          :to="item.to"
          >{{ item.title }}</v-btn
        ><v-spacer />
     
        <v-btn icon  class="white--text pe-5">
            En
        </v-btn>
      </v-app-bar>  
 
 </div>
</template>

<script>

import $ from "jquery";

export default {
    name: "SiteHeader",
    data() {
        return {
            clipped: false,
            drawer: false,
            items: [
                {
                   
                    title: "About Us",
                    to: "/about",
                },
                {
                  
                    title: "Services",
                    to: "/services",
                },
                {
                  
                    title: "Case Studies",
                    to: "/casestudies",
                },
                {
                   
                    title: "Contact Us",
                    to: "/contact",
                },
            ],
        };
    },
    methods: {
        handleScroll() {
            window.onscroll = () => {
                if ($(window).scrollTop() > 20)
                    $("header").addClass("header_sticky");
            };
        }
    },
    mounted() {
       
        this.handleScroll();
    },
   
}

</script>

<style scoped>
.submenubtn {
  cursor: default;
}
</style>
