<style scoped>
  @import "@/assets/css/sectorsCard.css";
</style>

<template>
    <div>
        <v-container>
            <v-row>
                <v-col xxl="6" xl="6" lg="6" md="6" sm="6" cols="12" v-for="(sector,index) in getSectors" :key="index">
                    <v-card  class="card_topic"  >
                      <v-img :src="index==0?require('@/assets/images/home/icon_card.svg'):require('@/assets/images/home/icon_card2.svg')"   max-width="96" max-height="96"  class="mx-auto" />
                       <v-card-title class="title_card"><span> {{sector.title}} </span></v-card-title>
                        <v-card-subtitle class="desc_card">
                            <p>
                               {{sector.full_text}}
                            </p>
                        </v-card-subtitle>
                          
                    </v-card>
                </v-col>
               
            </v-row>
        </v-container>
        </div>
  
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name:"ListSectors",
    methods: {
      ...mapActions(["fetchTopics"]),
    },
    computed:{
      ...mapGetters(["getSectors"]),
    },
    mounted() {
    let sectors={
      topic_type_id:20,
      cat:"sectors"
    }
    this.fetchTopics(sectors);
    },
}
</script>