<style scoped>
   @import "../assets/css/servicesCard.css";
</style>

<template>
    <div>
      <v-container>
            <v-row>
               <v-col cols="3"  class="ps-1" v-for="(service,index) in 4"  :key="index">
                  <v-card class="card_white_arrow" max-width="398" three-line >
                      <v-card-title class="title_card">
                           {{getListServices[index].title}}
                      </v-card-title>
                      <v-card-subtitle class="desc_card">
                           {{getListServices[index].brief_text}}
                       </v-card-subtitle>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-img :src="require('@/assets/images/home/small-see-more-white.png')" max-width="66" max-height="66" class="arrow_yellow_small"></v-img>
                      </v-card-actions>
                  </v-card>
              </v-col>
           </v-row>
        </v-container>
        </div>
 
</template>
<script>
 import { mapGetters, mapActions } from "vuex";
 export default{
   name:"ServicesList",
  methods: {
    ...mapActions(["fetchTopics"]),
  },
  computed:{
    ...mapGetters(["getListServices"]),
    
  },
  mounted() {
   let services={
    topic_type_id:30,
    cat:"list"
   }
   this.fetchTopics(services);
 },
 }

</script>