import { HTTP } from "@/plugins/http";
const state = {
  configs: [],
};
const getters = {
  getConfigs: (state) => state.configs,
};
const actions = {
  async fetchConfigs({ commit }) {
    await HTTP.get("/configs")
      .then((response) => {
        response=response.data.data;
        commit("setConfigs",response);
      })
      .catch((error) => {
        console.log("err", error);
      });
  },
  
};

const mutations = {
  setConfigs: (state, configs) => (state.configs= configs),       
}

export default {
  state,
  getters,
  actions,
  mutations,
};
