import { HTTP } from "@/plugins/http";
const state = {
slider:{},
sectors:[],
defs:[],
intro:{},
expertise:{},
header:{},
mvv:{},
vision:{},
mission:{},
values:{},
listServices:[],
listCaseStudy:[],
form:{},
info:{},
common:{}
};
const getters = {
  getSlider: (state) => state.slider,
  getSectors: (state) => state.sectors,
  getDefs: (state) => state.defs,
  getIntro: (state) => state.intro,
  getExpertise: (state) => state.expertise,
  getMvv: (state) => state.mvv,
  getVision:(state)=> state.vision,
  getMission:(state)=> state.mission,
  getValues:(state)=> state.values,
  getListServices:(state)=> state.listServices,
  getListCaseStudy:(state)=> state.listCaseStudy,
  getForm:(state)=> state.form,
  getInfo:(state)=> state.info,
  getHeader:(state)=> state.header,
  getCommon:(state)=> state.common,
};
const actions = {
  async fetchTopics({ commit },obj) {
    await HTTP.get(`/topics?filters[topic_type_id]=${obj.topic_type_id}&filters[category]=${obj.cat}`)
      .then((response) => {
        response=response.data.data;
        switch(obj.topic_type_id){
          case 10:
            switch(obj.cat){
              case "slider":
                commit("setSlider",response);
                break;
                case "def":
                commit("setDefs",response);
                break;
              
                case "list":
                commit("setListServices",response);
                break;
              }
            
            break;
          
           case 20:
            switch(obj.cat){
                case "header":
                commit("setHeader",response);
                break;
                case "sectors":
                commit("setSectors",response);
                break;
                case "intro":
                  commit("setIntro",response);
                break;
                case "expertise":
                  commit("setExpertise",response);
                break;
                case "mvv":
                  commit("setMvv",response);
                break;
                case "vision":
                  commit("setVision",response);
                break;
                case "mission":
                  commit("setMission",response);
                break;
                case "values":
                  commit("setValues",response);
                break;
            }
           
            break;
            case 30:
              switch(obj.cat){
                case "header":
                commit("setHeader",response);
                break;
                case "list":
                commit("setListServices",response);
                break;
               } 
              break;
              case 40:
                switch(obj.cat){
                  case "header":
                  commit("setHeader",response);
                  break;
                  case "list":
                  commit("setListCaseStudy",response);
                  break;
                }
               break;
               case 50:
                  switch(obj.cat){
                    case "form":
                      commit("setForm",response);
                    break;
                    case "info":
                    commit("setInfo",response);
                    break;
                  }
                break;
                case 51:
                  commit("setCommon",response);
                break;
          }
      
      })
      .catch((error) => {
        console.log("err", error);
      });
  },
  
};

const mutations = {     
  setSlider: (state, slider) => (state.slider= slider), 
  setSectors: (state, sectors) => (state.sectors= sectors),   
  setDefs:(state, def) => (state.defs= def), 
  setIntro: (state,intro) => (state.intro= intro), 
  setExpertise: (state,expertise) => (state.expertise= expertise), 
  setMvv: (state , mvv) => (state.mvv= mvv),
  setVision:(state,vision) => (state.vision= vision),
  setMission:(state,mission) => (state.mission= mission),
  setValues:(state , values )=> (state.values=values),
  setListServices:(state,service)=> (state.listServices=service),
  setListCaseStudy:(state,casestudy)=> (state.listCaseStudy=casestudy),
  setForm:(state,form)=> (state.form=form),
  setInfo:(state,info)=> (state.info=info),
  setHeader:(state,header)=> (state.header=header),
  setCommon:(state,common)=> (state.common=common),
}

export default {
  state,
  getters,
  actions,
  mutations,
};
